    <b-img slot="aside" :src="tweet.img" width="64" height="64" alt="placeholder" />
    <h5 class="mt-0"> <strong> {{tweet.name}}</strong> <small> {{tweet.handle}}</small></h5>
    {{tweet.tweet}}

    <div class="level-right">
        <a class="level-item">
           <span class="icon is-small"><i class="fas fa-heart"></i></span>
           <span class="likes"> {{tweet.likes}} </span>
        </a>
    </div>
  </b-media>
</div>  
</template>

<script>

export default{
        name: "remarksComponent",
        props: {
    		tweet: Object
  		}
}

 </script>

