.tweet {
  max-width: 600px!important;
  margin: 0 auto;
  border-radius: 5px; 
  -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.16),0 2px 10px 0 rgba(0,0,0,.12);
   box-shadow: 0 2px 5px 0 rgba(0,0,0,.16),0 2px 10px 0 rgba(0,0,0,.12);
   margin-bottom:20px;
   padding: 10px;
}

.tweet img {
  border-radius: 30px;
}

.likes {
  padding: 0 7.5px;
}

</style>